.portfolioTitle {
  color: #775ada;
  margin-bottom: 1rem;
}

.portfolioDescription {
  padding: 10px;
  text-justify: inter-word;
}

.portfolioSubTitle {
  color: #ff304f;
  margin-bottom: 1rem;
}

.portfolioSnippet {
  height: 26.7vw;
  display: flex;
  justify-content: center;
}

.preIntroBlank {
  height: 4rem;
}

.portfolioSubSectionBlank {
  height: 1rem;
}

.portfolioImg {
  padding: 1.16vw;
  width: 100%;
  height: 100%;
  border-radius: 2.17vw;
  transition: .5s ease;
}

.portfolioDescription > p {
  text-align: justify;
}