body {
  margin: 0;
  font-family: 'nunito-regular', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'metropolis-regular';
  src: url('./fonts/Metropolis/Metropolis-Regular.otf');
}

@font-face {
  font-family: 'metropolis-bold';
  src: url('./fonts/Metropolis/Metropolis-Bold.otf');
}

@font-face {
  font-family: 'metropolis-light';
  src: url('./fonts/Metropolis/Metropolis-Light.otf');
}

@font-face {
  font-family: 'nunito-regular';
  src: url('./fonts/Nunito/nunito/Nunito-Regular.ttf');
}

@font-face {
  font-family: 'nunito-bold';
  src: url('./fonts/Nunito/nunito/Nunito-Bold.ttf');
}

@font-face {
  font-family: 'nunito-light';
  src: url('./fonts/Nunito/nunito/Nunito-Light.ttf');
} 

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
