main {
  padding-bottom: 2rem;
}

section {
  padding: 1rem;
}

.selfIntroTitle {
  color: #775ada;
}

.preIntroBlank {
  height: 4rem;
}

.prePortfolioBlank {
  height: 4rem;
}

.projectsTitle {
  color: #ff304f;
  padding-bottom: 2rem;
}

.portfolioCol {
  position: relative;
  height: 100%;
}

.portfolioImgWrap {
  width: 100%;
  height: 100%;
  display: block;
}

.portfolioImg {
  padding: 1.16vw;
  width: 100%;
  height: 100%;
  border-radius: 2.17vw;
  transition: .5s ease;
}

.portfolioImgOverlay {
  width: calc(100% - 2.32vw);
  height: calc(100% - 2.32vw);
  border-radius: 14px;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fecea8;
  color: black;
}

.portfolioImgOverlayClinic {
  width: calc(100% - 2.32vw);
  height: calc(100% - 2.32vw);
  border-radius: 14px;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0ed5b2;
  color: black;
}

.portfolioImgOverlayChatApp {
  width: calc(100% - 2.32vw);
  height: calc(100% - 2.32vw);
  border-radius: 14px;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffc7d7;
  color: black;
}

.portfolioImgOverlayToDoList {
  width: calc(100% - 2.32vw);
  height: calc(100% - 2.32vw);
  border-radius: 14px;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #6886c5;
  color: black;
}

.portfolioCol:hover .portfolioImg {
  opacity: 0.2;
}

.portfolioCol:hover .portfolioImgOverlay,
.portfolioCol:hover .portfolioImgOverlayClinic,
.portfolioCol:hover .portfolioImgOverlayChatApp,
.portfolioCol:hover .portfolioImgOverlayToDoList {
  opacity: 0.8;
}